<template>
    <!-- :autoplay="2000" :wrap-around="true" -->
    <!-- Carousel element is section tag with class "carousel" -->
    <Carousel :autoplay="5000" :wrap-around="true" :transition="500" :touchDrag="false" :mouseDrag="false"> 
    <Slide v-for="slide in slides" :key="slide.id" class="slide">
      <div class="carousel__item" :style="{ backgroundImage: 'url(' + slide.image + ')', backgroundPosition: position(slide).background ? position(slide).background : undefined}">
        <div
          v-html="slide.title"
          class="title"
          :style="{ left: position(slide).left ? position(slide).left : undefined, top: position(slide).top ? position(slide).top : undefined, right: position(slide).right ? position(slide).right : undefined, bottom: position(slide).bottom ? position(slide).bottom : undefined }">
        </div>
      </div>
      <LandingInfo
        class="property-info"
        :hotels="hotels"
        :prosIsCard="prosIsCard"
        :name="slide.item.name"
        :title="slide.item.title" 
        :text="slide.item.text" 
        :pros="slide.item.pros"/>
    </Slide>

  </Carousel>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import { Carousel, Slide } from 'vue3-carousel';
import LandingInfo from '@/components/landing/LandingInfo.vue'

import 'vue3-carousel/dist/carousel.css';

export default defineComponent({
    components: { Carousel, Slide, LandingInfo },
    props: {
        items: {
            type: Array,
            required: true
        },
        prosIsCard: {
            type: Boolean,
            required: false,
            default: false
        },
        hotels: {
            type: Boolean,
            required: false,
            default: false
        },

    },
    setup() {
        
    },
    methods: {
        position(slide) {
            return this.isMobile ? slide.positionMobile : slide.position;
        }
    },
    data: ()=>({
        slides: []
    }),
    computed: {
        isMobile() {
            return this.$store.getters.isMobile
        },
    },
    mounted() {
        let counter = 1
        this.items.forEach((item)=>{
            this.slides.push({
                id: counter,
                image: require(`@/assets/newImages/${item.name}_large_photo.png`),
                title: '',//item.title,
                position: {left: '79px', bottom: '45px'},
                positionMobile: {top: '-110px'},
                item: item
            })
            counter+=1
        })
    }
})
</script>

